	var _self;
	import navbar from '@/components/navbar/navbar.vue'
	export default {
		components: {
			navbar
		},
		data() {
			return {
	            title: 'picker',
	            array: ['请下拉选择结果','不合格', '合格' ],
	            index: 0,//检疫结果(0:不合格，1：合格)
				checkName:"",//检疫项目名称
				productId:"",//商品id
				accountId:"",//	登录账号id
	            // 商品信息
				goodInfo:{}
			}
		},
		onLoad(e) {
			// console.log(e);
			this.getGoodsDetail(e.id);
			this.productId = e.id;
            _self = this;
			// console.log(this.productId);
			uni.getStorage({
			    key: 'useinfo',
			    success: function (res) {
			        console.log(res.data);
					_self.accountId = res.data.id
					// console.log(_self.accountId);
			    }
			});
		},
		methods:{			
			 bindPickerChange: function(e) {
			            console.log('picker发送选择改变，携带值为', e.target.value)
			            this.index = e.target.value
			 },
			 // 获取商品详情
			 async getGoodsDetail(id) {
			 	try{
			 		const res = await this.$util.request({ url: `/traceability_am/goods/detail?productId=${id}` });
			 		if(res.code === 200) {
			 			this.goodInfo = res.data.goodsInfo;
			 		}else {
			 			this.$util.showToast({title : res.message,icon : 'none'});
			 		}
			 	}catch(err){
			 		console.log(err);
			 		this.$util.showToast({title : '网络超时,请重试',icon : 'none'});
			 	}
			 },
			 //验证方法
			 checkDatalogin() {
			 	if (!this.checkName) {
			 		uni.showToast({
			 			title: '检疫项目名称不能为空',
			 			icon: 'none',
			 			mask: true,
			 			duration: 1000,
			 		});
			 		return false
			 	};	
				if (this.index == 0) {
					uni.showToast({
						title: '请下拉选择',
						icon: 'none',
						mask: true,
						duration: 1000,
					});
					return false
				};				
			 	return true
			 },
			 //商品抽样保存按钮
			 goodsSampleConfir(){
				 if(this.checkDatalogin()){
					 let _param = {
					 	checkName: this.checkName,
					 	checkResult: this.index-1,
						accountId:this.accountId,
						productId:this.productId,
					 };
					 console.log(_param)
					 this.$util.httpPost("/traceability_am/checkLog/add", _param).then(res => {
						
					 	if (Number(res.data.code) === 200) {
					 		uni.showToast({
					 			title: res.data.message,
					 			icon: 'success',
					 			mask: true,
					 			duration: 1000,
					 		});	
							//抽检保存成功后1s后返回原页面						
					 		setTimeout(() => {
							  uni.navigateBack()
					 		 },1000)   
					 		
					 	} else {
					 		uni.showToast({
					 			title: res.data.message,
					 			icon: 'none',
					 			mask: true,
					 			duration: 1000,
					 		})
					 	}
					 });
				 }
			 }
				 
		}
	
	}
